* {
    font-family: 'Open Sans', sans-serif !important;
  }

  .App {
    text-align: center;
  }

  .App-logo {
    -webkit-animation: App-logo-spin infinite 8s linear;
            animation: App-logo-spin infinite 8s linear;
    height: 120px;
  }

  .App-header {
    background-color: rgb(255, 255, 255);
    height: 150px;
    padding: 20px;
    color: rgba(63, 58, 58, 0.781);
  }

  .App-title {
    font-size: 1.5em;
  }

  .App-intro {
    font-size: large;
  }

  @-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }

  @keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }

  .full-container {
    height: 100vh;
    background: -webkit-gradient(linear, left top, right top, from(rgb(54, 20, 166)), to(rgb(33, 162, 233)));
    background: -webkit-linear-gradient(left, rgb(54, 20, 166) 0%, rgb(33, 162, 233) 100%);
    background: -o-linear-gradient(left, rgb(54, 20, 166) 0%, rgb(33, 162, 233) 100%);
    background: linear-gradient(to right, rgb(54, 20, 166) 0%, rgb(33, 162, 233) 100%);
  }



